@font-face {
  font-family: 'Roboto';
  src: local('Roboto Light'), local('Roboto-Light'), url('../fonts/roboto/Robotolight.woff2') format('woff2'), url('../fonts/roboto/Robotolight.woff') format('woff'), url('../fonts/roboto/Robotolight.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: 'Roboto';
  src: local('Roboto Medium'), local('Roboto-Medium'), url('../fonts/roboto/Robotomedium.woff2') format('woff2'), url('../fonts/roboto/Robotomedium.woff') format('woff'), url('../fonts/roboto/Robotomedium.ttf') format('truetype');
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: 'Roboto';
  src: local('Roboto Bold'), local('Roboto-Bold'), url('../fonts/roboto/Robotobold.woff2') format('woff2'), url('../fonts/roboto/Robotobold.woff') format('woff'), url('../fonts/roboto/Robotobold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
}
/*
LESS variables are information about icon's compiled state, stored under its original file name

.icon-home {
  width: @icon-home-width;
}

The large array-like variables contain all information about a single icon
@icon-home: x y offset_x offset_y width height total_width total_height image_path name;

At the bottom of this section, we provide information about the spritesheet itself
@spritesheet: width height image @spritesheet-sprites;
*/
/*
The provided classes are intended to be used with the array-like variables

.icon-home {
  .sprite-width(@icon-home);
}
.icon-email {
  .sprite(@icon-email);
}

Example usage in HTML:

`display: block` sprite:
<div class="icon-home"></div>

To change `display` (e.g. `display: inline-block;`), we suggest using a common CSS class:

// CSS
.icon {
  display: inline-block;
}

// HTML
<i class="icon icon-home"></i>
*/
/*
The `.sprites` mixin generates identical output to the CSS template
  but can be overridden inside of LESS

This must be run when you have at least 2 sprites.
  If run with a single sprite, then there will be reference errors.

.sprites(@spritesheet-sprites);
*/
html {
  line-height: 1.2;
  font-size: 14px;
}
body {
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
  font-family: 'Roboto', 'Arial' sans-serif;
  color: #263238;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
p {
  margin: 0;
}
a {
  text-decoration: none;
  color: black;
  outline: none;
}
audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}
textarea {
  resize: none;
}
section {
  position: relative;
  padding: 40px 0;
}
input,
select,
button {
  outline: none;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
.container {
  width: 100%;
  max-width: 1370px;
  margin: 0 auto;
  padding: 0 30px;
}
.icon {
  display: inline-block;
}
.logo {
  display: block;
  width: 80px;
}
.logo svg {
  width: 100%;
  height: 50px;
  fill: #fff;
}
.header-top.fixed .logo svg {
  fill: #437077;
}
.footer .logo svg {
  fill: #437077;
}
.button {
  width: 100%;
  max-width: 200px;
  padding: 20px;
  border: none;
  border-radius: 30px;
  background: #00ecf4;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  font-size: 1rem;
  font-weight: 700;
  box-shadow: 0 10px 30px rgba(20, 108, 113, 0.15), inset 0 0 2px rgba(255, 255, 255, 0.3);
  cursor: pointer;
  transition: 0.3s;
}
.button:hover {
  background-color: #00898e;
}
.preloader {
  position: fixed;
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  z-index: 10000;
  background-color: #00fffc;
  top: 0;
  left: 0;
}
.cssload-thecube {
  width: 73px;
  height: 73px;
  margin: 0 auto;
  margin-top: 49px;
  position: relative;
  transform: rotateZ(45deg);
}
.cssload-thecube .cssload-cube {
  position: relative;
  transform: rotateZ(45deg);
}
.cssload-thecube .cssload-cube {
  float: left;
  width: 50%;
  height: 50%;
  position: relative;
  transform: scale(1.1);
}
.cssload-thecube .cssload-cube:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  animation: cssload-fold-thecube 2.76s infinite linear both;
  transform-origin: 100% 100%;
}
.cssload-thecube .cssload-c2 {
  transform: scale(1.1) rotateZ(90deg);
}
.cssload-thecube .cssload-c3 {
  transform: scale(1.1) rotateZ(180deg);
}
.cssload-thecube .cssload-c4 {
  transform: scale(1.1) rotateZ(270deg);
}
.cssload-thecube .cssload-c2:before {
  animation-delay: 0.35s;
}
.cssload-thecube .cssload-c3:before {
  animation-delay: 0.69s;
}
.cssload-thecube .cssload-c4:before {
  animation-delay: 1.04s;
}
@keyframes cssload-fold-thecube {
  0%,
  10% {
    transform: perspective(136px) rotateX(-180deg);
    opacity: 0;
  }
  25%,
  75% {
    transform: perspective(136px) rotateX(0deg);
    opacity: 1;
  }
  90%,
  100% {
    transform: perspective(136px) rotateY(180deg);
    opacity: 0;
  }
}
.header {
  width: 100%;
  display: flex;
  align-items: center;
  min-height: 930px;
  position: relative;
  padding: 65px 0;
  background: url('../img/header-bg1.jpg') 50% 50% no-repeat;
  overflow: hidden;
}
.header-top {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 30px 0 0;
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
  z-index: 10000;
}
.header-top .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: 0.3s ease-in-out;
}
.header-top.fixed {
  position: fixed;
  padding: 15px 0;
  transition: 0.3s;
  background-color: #212e30;
  border-bottom: 1px solid #fff;
}
.header-top.fixed a {
  color: #437077;
}
.header-img {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 100;
  text-align: right;
}
.header-img img {
  width: 100%;
  max-width: 695px;
}
.header-img:before {
  position: absolute;
  top: -25px;
  left: 25px;
  display: block;
  content: '';
  width: 100%;
  height: 100%;
  background: url('../img/headen-shoes-bg.png') 50% 50% no-repeat;
  background-size: 130% 130%;
  z-index: -10;
}
.header-main {
  display: flex;
  justify-content: space-between;
  padding-left: 135px;
}
.header-nav {
  display: flex;
  align-items: center;
}
.header-toggle {
  display: block;
  width: 30px;
  height: 20px;
  position: relative;
  margin-left: 40px;
  cursor: pointer;
}
.header-toggle__line {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 3px;
  display: block;
  background-color: #fff;
  transition: 0.3s;
}
.header-top.fixed .header-toggle__line {
  background-color: #437077;
}
.header-toggle__line--middle {
  top: 8.3px;
  margin: auto 0;
}
.header-toggle__line--bottom {
  bottom: 0;
  top: auto;
}
.header.active .header-toggle__line {
  transition: .3s;
}
.header.active .header-toggle__line--top {
  top: 10px;
  transform: rotate(45deg);
}
.header.active .header-toggle__line--middle {
  top: 8.5px;
  opacity: 0;
}
.header.active .header-toggle__line--bottom {
  top: 10px;
  transform: rotate(-45deg);
}
.nav-list {
  display: flex;
  opacity: 0;
  width: 0;
  transition: background-color 1s;
}
.nav-item {
  margin-left: 10px;
}
.nav-link {
  display: none;
  padding: 15px 25px;
  color: #fff;
  text-transform: capitalize;
  font-size: 1.42rem;
  border-radius: 30px;
  transition: 0.3s;
  line-height: 1.2;
}
.nav-link:hover {
  background-color: #00ecf4;
  color: #fff;
}
.header.active .nav {
  align-items: baseline;
  transition: 0.3s;
}
.header.active .nav-list {
  display: flex;
  width: 100%;
  opacity: 1;
  transition: opacity 1s;
}
.header.active .nav-link {
  display: block;
}
.header-text {
  width: 100%;
  max-width: 430px;
  position: relative;
  z-index: 1000;
  display: flex;
  align-items: center;
}
.header-text__title {
  margin-bottom: 100px;
  font-size: 2.85rem;
  line-height: 50px;
  font-weight: 700;
  color: #00fffc;
}
.header-text__button--right {
  margin-left: 25px;
  border: 2px solid #fff;
  background: transparent;
  box-shadow: 0 10px 30px rgba(20, 108, 113, 0.15), inset 0 0 2px rgba(255, 255, 255, 0.3);
}
.header-text__button--right:hover {
  border: 2px solid transparent;
}
.technology {
  width: 100%;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
}
.technology-bg {
  width: 100%;
  max-width: 1215px;
  position: relative;
  margin: 0 auto;
  padding: 0 25px;
  background: url('../img/technology-bg.png') 65% 0 no-repeat;
  background-size: contain;
}
.technology:before {
  position: absolute;
  display: block;
  content: '';
  top: -230px;
  left: 0;
  width: 100%;
  height: 230px;
  background: url('../img/technology-before.png') 50% 0 no-repeat;
  z-index: -10;
}
.technology:after {
  position: absolute;
  display: block;
  content: '';
  bottom: -250px;
  left: 0;
  width: 100%;
  height: 400px;
  background: url('../img/technology-after.png') 80% 0 no-repeat;
  z-index: -10;
}
.technology-button {
  position: absolute;
  bottom: 100px;
  left: 40px;
}
.technology-text {
  font-weight: 500;
  color: #00fffc;
  position: absolute;
}
.technology-text--one {
  top: 20%;
  left: -5%;
}
.technology-text--two {
  top: -5%;
  right: 15%;
}
.technology-text--three {
  bottom: 65%;
  right: -2%;
}
.technology-img {
  width: 100%;
  max-width: 1080px;
  margin: 0 auto;
  display: block;
  position: relative;
}
.technology-img img {
  width: 100%;
}
.info {
  width: 100%;
  min-height: 900px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.info-flex {
  display: flex;
  position: relative;
  margin-top: 90px;
  z-index: 1000;
}
.info-img {
  display: block;
  width: 100%;
  max-width: 1290px;
  margin-left: -130px;
  margin-top: 70px;
}
.info-img img {
  width: 100%;
  display: block;
}
.info:before {
  position: absolute;
  display: block;
  content: '';
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('../img/info-before.png') 0 50% no-repeat;
  z-index: -100;
}
.info:after {
  position: absolute;
  display: block;
  content: '';
  bottom: 0;
  right: 0;
  width: 100%;
  height: 1210px;
  background: url('../img/info-after.png') 100% 0 no-repeat;
  background-size: 73% 100%;
  z-index: -100;
}
.info-item {
  width: 100%;
  max-width: 551px;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto 0;
  text-align: center;
}
.info-item__title {
  margin-bottom: 40px;
  font-size: 4.28rem;
  font-weight: 700;
}
.info-item__text {
  margin-bottom: 80px;
  font-size: 1.42rem;
  line-height: 30px;
  font-weight: 100;
}
.price {
  width: 100%;
  padding-bottom: 150px;
}
.price:before {
  position: absolute;
  display: block;
  content: '';
  top: -180px;
  left: 0;
  width: 100%;
  height: 205px;
  background: url('../img/price-before.png') 0 0 no-repeat;
  z-index: -100;
}
.price-img {
  display: block;
  width: 100%;
  max-width: 1250px;
  margin: 0 auto 155px;
}
.price-flex {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
}
.price-title {
  margin-bottom: 30px;
  font-size: 2.5rem;
  font-weight: 700;
}
.price-text {
  font-size: 1.42rem;
  font-weight: 100;
  margin-bottom: 30px;
}
.price-dol {
  margin-right: 30px;
  font-size: 2.5rem;
  font-weight: 700;
  color: #f50057;
  vertical-align: middle;
}
.price-button {
  max-width: 130px;
  padding-top: 15px ;
  padding-bottom: 15px;
}
.price-right {
  text-align: right;
}
.price-list {
  display: flex;
  justify-content: flex-end;
  padding: 15px 30px;
  margin-bottom: 20px;
  border-radius: 5px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.06);
}
.price-item {
  margin-left: 35px;
  cursor: pointer;
}
.price-item:first-child {
  margin-left: 0;
}
.price-item .icon {
  background-image: url(../img/sprite/sprite.png);
  background-position: -32px -28px;
  width: 16px;
  height: 16px;
}
.price-item .icon--half {
  background-image: url(../img/sprite/sprite.png);
  background-position: -16px -28px;
  width: 16px;
  height: 16px;
}
.price-item .icon--boreder {
  background-image: url(../img/sprite/sprite.png);
  background-position: 0px -28px;
  width: 16px;
  height: 16px;
}
.price-review {
  font-size: 1.28rem;
  font-weight: medium;
  color: #00fffc;
}
.footer {
  padding: 40px 0;
  background-color: #212e30;
}
.footer .logo {
  margin-right: 50px;
}
.footer-flex {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
}
.footer-left {
  display: flex;
  align-items: center;
}
.footer-link {
  color: #437077;
  line-height: 30px;
  font-weight: 100;
}
.footer-list {
  padding: 0 20px;
  margin: 0 30px;
}
.footer-social {
  display: flex;
}
.footer-social__item {
  margin-left: 15px;
}
.footer-social__item:first-child {
  margin-left: 0;
}
.footer-social__link {
  display: block;
  padding: 10px;
  line-height: 0 important;
}
.footer-social__link svg {
  width: 15px;
  height: 15px;
  fill: #437077;
}
@media only screen and (max-width: 1180px) {
  html {
    font-size: 12px;
  }
  .header-main {
    flex-wrap: wrap-reverse;
    justify-content: space-around;
    align-items: center;
    padding-left: 0;
  }
  .header-img {
    max-width: 500px;
  }
  .header-text__title {
    margin-bottom: 50px;
    text-align: center;
  }
  .info {
    min-height: 700px;
    align-items: flex-start;
  }
  .info:after {
    background-size: 100% 100%;
    right: -200px;
  }
}
@media only screen and (max-width: 992px) {
  .header {
    min-height: auto;
  }
  .header-main {
    margin-bottom: 100px;
  }
  .header-text__title {
    margin-bottom: 50px;
    text-align: center;
  }
  .technology:before {
    top: -149px;
    background-size: 100% 65%;
  }
  .technology:after {
    bottom: -200px;
    height: 200px;
  }
  .technology-button {
    bottom: 0;
    left: 0;
  }
  .info {
    min-height: auto;
  }
  .info-flex {
    flex-wrap: wrap-reverse;
    justify-content: center;
    margin: 120px 0;
  }
  .info-img {
    margin-top: 40px;
    margin-left: 0;
  }
  .info-item {
    position: static;
  }
  .info-item__title {
    margin-bottom: 20px;
  }
  .info-item__text {
    margin-bottom: 40px;
  }
  .info:before {
    background-size: 75% 100%;
  }
  .info:after {
    height: 100%;
  }
  .price {
    padding-bottom: 60px;
  }
  .price-img {
    margin-bottom: 50px;
  }
  .price-flex {
    justify-content: center;
  }
  .price-left {
    width: 100%;
    text-align: center;
    margin-bottom: 60px;
  }
  .footer-list {
    width: 100%;
    max-width: 110px;
    margin: 0;
  }
  .footer .logo {
    margin-right: 0;
  }
}
@media only screen and (max-width: 768px) {
  .header {
    padding: 10px 0;
  }
  .header-top {
    transition: 1s;
  }
  .header.active .header-top {
    background-color: #00fffc;
    transition: 1s;
  }
  .header-main {
    margin-top: 150px;
  }
  .nav-list {
    position: absolute;
    top: 110px;
    left: 0;
    text-align: center;
    background-color: #00fffc;
    border-top: 1px solid #437077;
  }
  .header.active .nav-list {
    display: block;
  }
  .header-top.fixed .nav-list {
    top: 80px;
  }
  .nav-item {
    margin: 0;
    border-bottom: 1px solid #437077;
  }
  .nav-link {
    border-radius: 0;
    text-transform: uppercase;
  }
  .technology:before {
    top: -114px;
    background-size: 100% 50%;
  }
  .technology:after {
    bottom: -199px;
    height: 200px;
    background-size: 100% 65%;
  }
  .price:before {
    height: 140px;
    top: -140px;
    background-size: 100% 100%;
  }
  .footer-list {
    width: 100%;
    max-width: 120px;
  }
  .footer-social {
    margin-top: 20px;
  }
}
@media only screen and (max-width: 512px) {
  html {
    font-size: 10px;
  }
  .info-flex {
    margin: 60px 0;
  }
  .info:after {
    right: -120px;
  }
  .info-item__title {
    font-size: 30px;
  }
  .price:before {
    height: 90px;
    top: -90px;
  }
  .footer-left {
    width: 100%;
    margin-top: 20px;
    margin-left: -15px;
  }
  .footer-social {
    margin-top: 40px;
  }
}
@media only screen and (max-width: 489px) {
  .header {
    height: 100vh;
  }
  .header-text__button {
    display: block;
  }
  .header-text__button--right,
  .header-text__button {
    margin: 0 auto;
  }
  .header-text__button--right {
    margin-top: 20px;
  }
  .technology:before {
    top: -79px;
    height: 160px;
  }
  .technology:after {
    bottom: -149px;
    height: 150px;
  }
  .technology-button {
    display: block;
    position: static;
    margin: 20px auto 0;
  }
}
@media only screen and (max-width: 400px) {
  html {
    font-size: 8px;
  }
  .header-img {
    max-width: 230px;
  }
  .price:before {
    height: 90px;
    top: -89px;
  }
}
@media only screen and (max-width: 400px) {
  .header-text__title {
    margin-bottom: 0;
  }
  .header-main {
    margin-top: 75px;
  }
}
